@mixin boxShadow {
    -webkit-box-shadow: 0px 0px 9px 0px rgba(0,0,0,0.38);
    -moz-box-shadow: 0px 0px 9px 0px rgba(0,0,0,0.38);
    box-shadow: 0px 0px 9px 0px rgba(0,0,0,0.38);
}

$lightGrey: #D3D3D3;

.messages {
    &-outer {
        display: flex;
        width: 100%;
        padding: 10px;
    }

    &-inner {
        width: 100%;
        height: 620px;
        border: 1px solid $lightGrey;
        border-radius: 4px;
        display: flex;

        &-left {
            display: flex;
            flex-direction: column;
            font-size: 12px;
            border-right: 1px solid $lightGrey;

            &-top {
                display: flex;
                align-items: center;
                li {
                    padding: 12px 8px;
                    font-weight: bold;
                    cursor: pointer;
                    &:not(:last-child) {
                        border-right: 1px solid $lightGrey;             
                    }

                    &:hover {
                        background-color: #E3E3E3;
                        transition: .3s;
                    }
                    border-bottom: 1px solid $lightGrey;
                }
            }

            &-search {
                display: flex;
                align-items: center;
                justify-content: center;
                margin-top: 5px;
                padding: 20px 20px 20px 10px;
                margin: 5px;
                max-width: 300px;
                width: 100%;
                height: 50px;
                @include boxShadow;     

                &-inner {
                    width: 100%;
                    &-input {
                        width: 100%;
                        font-size: 12px;
                        border: 1px solid $lightGrey;
                        border-radius: 1.5px;
                        padding: 3px;
                        &:focus {
                            outline: none;
                        }
                    }
                }
            }
        }

        &-right {
            width: 100%;
            display: flex;
            flex-direction: column;
    
            &-newMsg {
                display: flex;
                justify-content: flex-end;
                padding: 6px;
                
                &-btn {
                    font-size: 12px;
                    background-color: #47924A;
                    border: none;
                    border-radius: 2px;
                    padding: 5px;
                    text-align: center;
                    color: #fff;
                    cursor: pointer;
                    &:hover {
                        opacity: .8;
                    }
                }
            }

            &-chat {
                width: 100%;
                height: 100%;

                &-inner {
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    width: 100%;
                    height: 100%;
                    
                    &-field {
                        border-top: 1px solid $lightGrey;
                        height: 100%;
                        text-align: center;
                        font-size: 10px;
                        padding: 50px;
                    }

                    &-msgArea {
                        background-color: #F6F7F9;
                        padding: 10px;
                        height: 200px;
                        &-textArea {
                            #msgTextAreaId {
                                border: 1px solid $lightGrey;
                                border-radius: 3px;
                                width: 100%;
                                font-size: 12px;
                                resize: none;
                                height: 60px;
                                &:focus {
                                    outline: none;
                                }
                            }
                        }
                        &-footer {
                            display: flex;
                            justify-content: space-between;
                            align-items: center;
                            height: auto;
                            padding: 5px;
                            
                            &-uploadFile {
                                display: flex;
                                background-color: $lightGrey;
                                border-radius: 2.5px;

                                &:hover {
                                    opacity: .9;
                                }

                                &-label {   
                                    padding: 5px;                    
                                    cursor: pointer;
                                    font-size: 12px;
                                    #fileUploadIcoId {
                                        margin-right: 5px;                              
                                    }
                                }
                            }
                            &-send {
                                border-radius: 2.5px;
                                &-btn {
                                    background-color: $lightGrey;
                                    border: none;
                                    padding: 5px;
                                    font-size: 12px;
                                    cursor: pointer;

                                    #sendIcoId {
                                        margin-right: 5px;
                                    }
                                    &:hover {
                                        opacity: .9;
                                    }

                                    &:focus {
                                        outline: none;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
