$lightGrey: #D3D3D3;

.userPage {
    &-outer {

    }

    &-inner {
        display: flex;
        height: 95vh;
        padding: 5px;

        &-aside {
            font-size: 12px;
            text-align: center;
        }

        &-main {
            margin-left: 3px;
            width: 100%;
            border: 1px solid $lightGrey;
            border-radius: 2px;
            padding: 30px 0px 20px 10px;
            height: 620px;

            &-wrapper {
                width: 90%;
                height: 100%;
                padding: 20px;
                border-top: 1px solid $lightGrey;

                &-grid {
                    display: grid;
                    width: 100%;
                    height: 80%;
                    grid-template-columns: fit-content(40%) 1fr;
                    column-gap: 10px;
                    row-gap: 10px;
                    align-items: center;
                    font-size: 12px;

                    .phoneInputWrapper {
                        display: flex;
                        align-items: center;

                        #phoneInpId {
                            margin-right: 5px;
                            &:focus {
                                outline: none;
                            }
                        }

                        .savePhoneBtn {          
                            background-color: $lightGrey;
                            border: none;
    
                            &:hover {
                                opacity: .9;
                            }
                        }
                    }       
                }
            }
        }
    }
}