$lightGrey: #D3D3D3;
$darkGreen: #2D4D50;
$white: #FFFFFF;

@mixin grid-template {
    grid-template-columns: 300px 170px 60px 60px 80px 100px 80px 50px;
}

.semestersInfo {
    &-outer {
        max-width: 1120px;
        height: 100%;
        padding: 20px 20px 0 20px;
    }

    &-inner {
        width: 100%;
        height: 100%;
        &-top {
            color: $white;
            width: 100%;
            height: 100%;
            background-color: $darkGreen;
            padding: 5px;
            text-align: center;
            border-radius: 2px;
        }

        &-gridHead {
            width: 100%;
            display: grid;
            align-items: center;
            @include grid-template;
            font-size: 12px;

            &-item {
                padding: 5px;
                display: flex;
                align-items: center;
                height: 100%;
                border-bottom: 1px solid $lightGrey;
                font-weight: bold;

                &:nth-child(3),
                &:nth-child(4),
                &:nth-child(5),
                &:nth-child(6),
                &:nth-child(7),
                &:nth-child(8) {
                    justify-content: center;
                }

                &:nth-child(1),
                &:nth-child(2) {
                    justify-content: flex-start;
                }
            }
        }

        &-gridMain {
            width: 100%;
            height: 100%;
            display: grid;
            @include grid-template;
            font-size: 12px;
            align-items: center;
            vertical-align: middle;

            &-item {
                padding: 5px;
                display: flex;
                align-items: center;
                min-height: 50px;
                height: 100%;
                border-bottom: 1px solid $lightGrey;

                &:nth-child(3),
                &:nth-child(4),
                &:nth-child(5),
                &:nth-child(6),
                &:nth-child(7),
                &:nth-child(8) {
                    justify-content: center;
                }

                &:nth-child(5) {
                    font-weight: bold;
                }

                &:nth-child(6) a,
                &:nth-child(7) a {
                    color: #1e789d;
                    &:visited {
                        color: #1e789d;
                    }
                }

                &:last-child {
                    color: #1A95BD;
                    cursor: pointer;
                }
            }
        }
    }
}

.mainApp-inner-activePage-userSemestersInfo {
    display: flex;
    flex-direction: column-reverse;
}

.userMarkPassed {
    color: #2e7d32;
}

.userMarkNotPassed {
    color: #d50000;
}

.userMarkFX {
    color: #ff6d00;
}
