@import './styleTools/normalize.scss';
@import './styleTools/reset.scss';
@import './ComponentsStyles/Messages.scss';
@import './ComponentsStyles/UserSemestersInfo.scss';

$icoColor: #00897b;
$navBackgroundColor: #EFEFEF;
$navBackgroundColorHover: #E3E3E3;

// === Global >
*,
*::after,
*::before {
    box-sizing: border-box;
}
// === Global <

// === Fonts >
@font-face {
    font-family: 'mrglovani';
    src: url(./../fonts/bpg_mrgvlovani_caps_2010.ttf) format('truetype');
}
//  === Fonts <

.mainApp {
    &-outer {

    }

    &-inner {
        display: flex;
        font-family: 'mrglovani';

        &-nav {
            &-wrapper {
                display: flex;
                flex-direction: column;
                list-style: none;

                &-item {
                    display: flex;
                    flex-direction: column;
                    padding: 40px 0;
                    width: 150px;
                    text-align: center;
                    cursor: pointer;
                    user-select: none;
                    background-color: $navBackgroundColor;

                    &:hover {
                        background-color: $navBackgroundColorHover;
                        transition: .3s;
                    }

                    &-ico {
                        color: $icoColor;
                        font-size: 36px;
                        margin-bottom: 10px;
                    }

                    &-text {
                        color: black;
                        font-size: 12px;
                    }
                }
            }
        }

        &-activePage {
            max-width: 1000px;
            width: 100%;
        }
    }

}

.active {
    background-color: $navBackgroundColorHover;
}

.inActive {
    background-color: $navBackgroundColor;
}
